<!-- icon
icon:
1. fontAwesome 5fas far fab classclass : fas fa-user
2. material.iofa-*class
materialfa-2x
-->
<template>
  <i v-if="icon" :class="checkIcon(icon)" class="fa-fw" aria-hidden="true">
    {{ iconText }}
  </i>
</template>

<script>
export default {
  name: "icon",
  props: ["icon"],
  methods: {
    // iconclass
    checkIcon(icon) {
      // icon
      if (icon) {
        // 
        const string = icon.toLowerCase();
        // 
        const fa = string.indexOf("fa-");
        // fontAwesomefaicon
        // classmaterial
        if (fa > -1) {
          return icon;
        }
        return `material-icons ${icon}`;
      }
      return "";
    },
  },
  computed: {
    // icon
    iconText() {
      const { icon } = this;
      if (icon) {
        // 
        const string = icon.toLowerCase();
        // 
        const fa = string.indexOf("fa-");
        // fontawesomematerial.ioicon
        if (fa === -1) {
          return icon;
        }
      }
      return "";
    },
  },
};
</script>


