/**
 *  i18n messages 
 * @param requireContext modules (/locale/{lang}.json)
 */
function allLocale(requireContext: any) {
  const fileNames = requireContext.keys();
  return fileNames.reduce((acc: any, cur: any) => {
    /**  */
    const modulePath: string[] = cur.split("/"); // array
    /** locale- */
    const module = modulePath[
      modulePath.findIndex(x => x === "locale") - 1
    ].split("-")[2];
    const locale: string = cur.match(/locale\/(\S*).json/)[1]; // 

    if (typeof acc[locale] !== "object") {
      acc[locale] = {};
    } // 

    acc[locale][module] = requireContext(cur); // [][]
    return acc;
  }, {});
}

/**
 * i18n messages
 */
const messagesG = allLocale(
  require.context("@/modules", true, /\/locale\/[\w]+.json$/),
);
/** element */
const messages: any = setElementLang(messagesG);

const defaultLocale = "zh_TW";
const localLocale: string = localStorage.getItem("lang") || defaultLocale; // local

const i18n = createI18n({
  legacy: false,
  locale: localLocale, // 
  fallbackLocale: {
    default: [defaultLocale],
    id: ["en", defaultLocale],
  }, // 
  messages,
  silentTranslationWarn: true, // 
  silentFallbackWarn: true, // 
});

// setTimeout(() => {
//   i18n.locale = localStorage.getItem('lang') || '';
// }, 2000);

export default i18n;
