/**  html  */
export const changeHtmlLang = (lang: string) => {
  const html = document.documentElement; // returns the html tag

  const htmlLang = langs[lang];
  if (htmlLang) {
    html.setAttribute("lang", htmlLang);
    return;
  }
  html.setAttribute("lang", lang);
};

