import { ref } from "vue";
import { ElForm } from "node_modules/element-ui/types/form";

/** EL-FORM  */
export default () => {
  const REF_FORM = ref(null);

  /**  */
  const submitForm = () => {
    const M = REF_FORM.value as unknown;
    const FORM = M as ElForm;
    return FORM.validate();
  };

  /**
   *  el-form-item  prop
   *  form 
   */
  const reset = () => {
    const M = REF_FORM.value as unknown;
    const FORM = M as ElForm;
    return FORM.resetFields();
  };

  return {
    REF_FORM,
    submitForm,
    reset,
  };
};
