/* eslint-disable @typescript-eslint/no-namespace */
/* eslint-disable camelcase, no-use-before-define */
import { ElMessage as $message } from 'element-plus'; // 
import $i18n from '@/modules/FPKG-230000-BackendCore/lang';
import $http from '@/modules/FPKG-230000-BackendCore/prototypes/axios'; // 
import errorCode from '../../errorCode'; // error code
import { typeList } from './Lists';
const API = 'blacklist/config/create';

export namespace typeCreate {
  export type request = {
    /**   PKG-410000-Blacklist/src/Constant/Type.php  */
    p41_bst_type: typeList.TYPE;
    /**  */
    p41_bst_sub_type?: string;
    /**    */
    p41_bst_content: string;
    /** (hidden)  UI{#}e.g. IP#1.2.3.4{##}e.g. COMMUNICATION_SOFTWARE#LINE_ID#@abc123 */
    p41_bst_feature: string;
    /**  max:200 */
    p41_bst_memo?: string;
  };
}

/**  */
const Create = async (request: typeCreate.request) => {
  return $http
    .post(API, request)
    .then((d) => {
      if (d.data.code === 0) {
        $message.success($i18n.global.t('Util.createComplete'));
        return d;
      }
      throw d;
    })
    .catch((d) => {
      errorCode(d.data);
    });
};

export default Create;
